<docs>
  # PageLegal
  > Legal page -> same template for termsofuse && privacyPolicy
  
  @author kevin <kevin@kffein.com>
</docs>
  
<script lang="ts">
import Hero from 'components/ui/Hero/index.vue';
import usePageFlow from 'utils/flow/page';
import { defineComponent, provide, ref } from 'vue';
import { useRoute } from 'vue-router';

export default defineComponent({
  name: "PageTerms",
  components: {
    Hero,
  },
  setup() {
    const route = useRoute()
    const hero = ref<any>()
    const { readyToRender, getData, /* onInitUi */ } = usePageFlow<PageLegal>({
      api: {
        url: `pages/${route.meta.section}/${route.meta.id}`,
        moc: true,
        getMoc(/*locale?:string*/) {
          return import(`./moc/page`); // `./moc/${locale}`
        },
        format: (response: any) => {
          if (response?.data?.title) {
            response.data.hero = {
              title: response.data.title
            };
          } else if (response?.title) {
            response.hero = {
              title: response.title
            };
          }
          return response;
        },
      },
    });

    const page = getData();
    provide("page", page);

    return {
      readyToRender,
      page,
      hero,
    };
  },
});
</script>

<template>
  <main v-if="readyToRender"
        ref="el"
        class="page legal"
  >
    <hero ref="hero" class="hero" />
    <div class="_container content">
      <div class="wysiwyg" v-html="page.text" />
    </div>
  </main>
</template>
  
